.article{
    position: relative;
    width:100%;
    background-color: white;
}
.article p{
    display: none;
}
.article p a{
    padding-top:5px;
    display: block;
    color: orangered;
    text-decoration: underline;
    font-style: italic
}
.article p a:hover{
    color: orange;
}
.article:hover p{
    display: block;
    font-weight: normal;
    height: inherit;
}
.article:hover h4{
    background-color: rgba(255,255,255,0.9);
    height: inherit;
    position: absolute;
    overflow: auto;
    background-image: none;
    top:0;
    color:black;
    animation: slidedown .3s forwards ease-in-out
}
@keyframes slidedown{
    0%{
        transform: translateY(-500px);
        z-index: -100;
    }
    100%{
        transform: translateY(00px);
        
    }
}
.article img{
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 2;
    object-fit: cover;
}

.article  h4{
    position: absolute;
    width: inherit;
    bottom: 0;
    left:0;
    padding: 10px;
    box-sizing: border-box;
    font-weight: bold;
    margin: 0px;
    font-size: 1rem;
    color:white;
    z-index: 3;
    background-image: linear-gradient(transparent,#000)
}
.article small{
    display: block;
    padding:10px;
    font-weight:normal;
    font-size: 15px;
    color:inherit;
    z-index: 3;
}
